@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:400,400i);
:root {
  --lt-pink: #FFEBFF;
  --med-pink: #D6B4D6;
  --bg-navy: #1B182E;
  /* --bg-navy: #19162f; */
}

* {
  box-sizing: border-box;
}

html, body {
  background: #1B182E;
  background: var(--bg-navy);
  margin: 0 auto;
  height: 100%;
  font-family: "Roboto Mono", monospace;
  color: #D6B4D6;
  color: var(--med-pink);
}

body {
  position: relative;
}

p {
  color: #D6B4D6;
  color: var(--med-pink);
  /* font-size: 14px; */
  font-size: 0.8em;
}

.page-container {
  position: absolute;
  left: 5.0em;
  top: 4.0em;
  width: calc(100% - 10.0em);
  height: calc(100% - 8.0em);
}

#canvas {
  position: absolute;
}
.nav {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 4em;
}

span#name-label {
  position: absolute;
  left: 0;
}

.nav-items ul {     
  margin: 0;
  padding: 0;
}

.nav-items li {
  list-style-type: none;
  display: inline-block;
  position: relative;
  margin-left: 4em;
}
 
a, a:visited { 
  text-decoration: none;
  color: var(--med-pink);
  text-transform: uppercase;
  font-size: 0.9em;
  letter-spacing: 1.5px;
}

.underline:hover:after,
.underline:focus:after,
.underline:active:after {
  width: 100%;
}

.underline:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -3px;
  height: 1px;
  background: var(--lt-pink);
  width: 0;
  -webkit-transition: width 0.25s;
  transition: width 0.25s;
}
#info {
  max-width: 450px;
  line-height: 22px;
  position: absolute;
  bottom: 0;
}

.borderline {
  position: relative;
  height: 2px;
  margin-bottom: 24px;
  background: var(--med-pink);
}

span#info-label {
  color: var(--lt-pink);
  letter-spacing: 1.2px;
  font-size: 22px;
}

div#info-content {
  margin-top: 24px;
  margin-bottom: 28px;
}

img#ig-logo {
  height: 22px;
  width: 22px;
  margin-right: 16px;
}

img#email-logo {
  height: 22px;
}
.inner-page-container {
  margin-bottom: 70px;
  overflow-x: auto;
}

.page-header {
  position: relative;
}

h1.page-header-label {
  font-size: 24px;
  letter-spacing: 1.5px;
  color: var(--lt-pink);
  display: inline-block;
}

div.page-header-divider {
  display: inline-block;
	height: 2px;
	background: var(--lt-pink);
  width: 48px;
  left: 128px;
	position: absolute;
	top: 50%;
}

div.page-content {
  line-height: 26px;
  max-width: 550px;
  margin-left: 128px;
}

/* Specific to Work page */
div.work-page-content {
  margin-top: 48px;
}
#projects-container {
  margin-top: 2.4em;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.project-container {
  overflow: hidden;
  margin-bottom: 64px;
}
.project-img {
  width: 72%;
  float: left;
}

img.project-highlight {
  width: 100%;
  height: auto;
}

.project-meta {
  float: right;
  width: 25%;
  overflow:hidden;
}

.project-description {
  border: 2px solid var(--med-pink);
  padding: 12px;
}

.project-description {
  margin-top: -2px;
}

.project-description p {
  color: var(--lt-pink);
  margin: 0px;
  line-height: 22px;
}

/* GRID VIEW */
.content-grid-item-outer {
  width: 31%;
  margin-bottom: 8em;
  padding: 0px;  
  min-width: 400px;
}

.project-img-grid {
  width: 100%;
}

.project-meta-container {
  margin-top: 1.2em;
  position: relative;
}

.project-name {
  text-transform: uppercase;
  color: var(--lt-pink);
  font-size: 1.1em;
  letter-spacing: 0.02em;
}

a.project-link {
  color: var(--lt-pink);
  display: flex;
  flex-direction: column;
  position: absolute;
  /* border-bottom: 1px solid grey;
  flex-grow: 2; */
}

a.project-link:after {
  content: "";
  bottom: -3px;
  height: 1px;
  width: 100%;
  background: var(--lt-pink);
}

.project-desc-container {
  padding-top: 2.5em;
}

.project-desc-container p {
  margin: 0px;
  line-height: 1.6em;
  color: var(--lt-pink);
}
